import PropTypes from 'prop-types'
import MaterialIcon from '@material/react-material-icon'
import TopAppBar, {
  TopAppBarIcon,
  TopAppBarRow,
  TopAppBarSection,
  TopAppBarTitle,
} from '@material/react-top-app-bar'

const AppBar = ({ isMobile, setIsDrawerOpen }) => (
  isMobile ? (
    <TopAppBar className='appbar'>
      <TopAppBarRow>
        <TopAppBarSection align='start'>
          <TopAppBarIcon navIcon tabIndex={0}>
            <MaterialIcon
              hasRipple
              icon='menu'
              onClick={() => setIsDrawerOpen(true)}
            />
          </TopAppBarIcon>
          <TopAppBarTitle>Mobile Title</TopAppBarTitle>
        </TopAppBarSection>
      </TopAppBarRow>
    </TopAppBar>
  ) : (
    <TopAppBar className='appbar'>
      <TopAppBarRow>
        <TopAppBarSection align='start'>
          <TopAppBarTitle>Desktop Title</TopAppBarTitle>
        </TopAppBarSection>
      </TopAppBarRow>
    </TopAppBar>
  )
)
AppBar.propTypes = {
  isMobile: PropTypes.bool.isRequired,
  setIsDrawerOpen: PropTypes.func.isRequired,
}

export default AppBar
